<template>
  <div
    class="custom-select"
    :tabindex="tabindex"
    @blur="open = false"
  >
    <div
      class="selected"
      :class="{open: open}"
      @click="open = !open"
    >
      {{ selected }}
    </div>
    <div
      class="items"
      :class="{selectHide: !open}"
    >
      <div
        class="item"
        v-for="(option, i) of options"
        :key="i"
        @click="selected=option; open=false; $emit('input', option)"
      >
        {{ option }}
      </div>
    </div>
  </div>
</template>


<script>
export default {
  props:{
    options:{
      type: Array,
      required: true
    },
    tabindex:{
      type: Number,
      required: false,
      default: 0
    }
  },
  data() {
    return {
      selected: this.options.length > 0 ? this.options[0] : null,
      open: false
    };
  },
  mounted(){
    this.$emit('input', this.selected);
  }
};
</script>

<style scoped>

.custom-select {
  position: relative;
  width: 100%;
  text-align: left;
  outline: none;
  height: 38px;
  line-height: 38px;
}

.selected {
  border-radius: 6px;
  border-right: 2px solid transparent;
  border-left: 2px solid transparent;
  border-top: 2px solid transparent;
  border-bottom: 2px solid #efefef;
  color: rgba(9,15,30, 0.54);
  padding-left: 8px;
  cursor: pointer;
  user-select: none;
}

.selected.open{
  border: 2px solid #653AF8;
  border-radius: 6px 6px 0px 0px;
}
.selected.open:after{
    transform: rotate(180deg);
}
.selected:after {
  position: absolute;
  content: "";
  top: 10px;
  right: 10px;
  font-size: 16px;
  line-height: 20px;
  opacity: 0.54;
  width: 18px;
  height: 20px;    
  transition: transform .3s;
}

.items {
  color: #090f1e;
  border-radius: 0px 0px 6px 6px;
  overflow: hidden;
  border-right: 2px solid #653AF8;
  border-left: 2px solid #653AF8;
  border-bottom: 2px solid #653AF8;
  position: absolute;
  background-color: white;
  left: 0;
  right: 0;
}

.item{
  color: #090f1e;
  padding-left: 8px;
  cursor: pointer;
  user-select: none;
}

.item:hover{
  background-color: lightGray;
}

.selectHide {
  display: none;
}
</style>