<template>
    <div id="questions">
        <p class="header">
              .
        </p>
        <div class="q-list">
            <div class="question" 
                v-for="(item, index) in question" 
                :key="index"  
                @mouseenter="item.hover = true" 
                @mouseleave="item.hover = false"
                :class="item.hover ? 'hover' : ''"
            >
                <div class="quest" 
                    @click="changeQuestion(index)" 
                    :class="opened == index ? 'active' : ''"
                >
                    <p>{{ question[index].quest }}</p>
                    <img 
                        v-if="item.hover"
                        src="@/assets/img/ui/WhitePlus.svg"
                        alt=""
                    />
                    <img 
                        v-else
                        src="@/assets/img/ui/Plus.svg"
                        alt=""
                    />
                </div>
                <transition
                    name="question"
                    v-bind:css="false"
                    v-on:before-enter="beforeEnter"
                    v-on:enter="enter"
                    v-on:leave="leave"
                >
                    <p class="answer" v-if="opened == index" v-html="question[index].answer"></p>
                </transition>
            </div>
        </div>
    </div>
</template>

<script>
import Velocity from 'velocity-animate';
export default {
    data() {
        return{
            opened: 0,
            question: [{
                quest: '   ?',
                answer: 'C       ( ),           .<br>         ( *).',
                hover: false
            },
            {
                quest: '   ?',
                answer: '-',
                hover: false
            },
            {
                quest: '     ?',
                answer: '-',
                hover: false
            },
            {
                quest: '      ?',
                answer: '-',
                hover: false
            },
            {
                quest: '       ?',
                answer: '-',
                hover: false
            },
            {
                quest: '   ?',
                answer: '-',
                hover: false
            },
            {
                quest: '   ?',
                answer: '-',
                hover: false
            }
        ]
        }

    },
    methods: {
        changeQuestion(index) {
            this.opened = index;
        },
        beforeEnter: function (el) {
            el.style.opacity = 0
            el.style.height = 0
        },
        enter: function (el, done) {
            var delay = 200;
            const width = getComputedStyle(el).width;

            el.style.width = width;
            el.style.position = 'absolute';
            el.style.visibility = 'hidden';
            el.style.height = 'auto';
      
            const height = getComputedStyle(el).height;
            
            el.style.width = null;
            el.style.position = null;
            el.style.visibility = null;
            el.style.height = 0;
      
            getComputedStyle(el).height;

            setTimeout(function () {
                Velocity(
                    el, {
                        opacity: 1,
                        height: height
                    }, {
                        complete: done
                    }
                )
            }, delay)
        },
        leave: function (el, done) {
            var delay = 200;
            setTimeout(function () {
                Velocity(
                    el, {
                        opacity: 0,
                        height: 0
                    }, {
                        complete: done
                    }
                )
            }, delay)
        }
    }
}
</script>