<template>
    <div id="welcome" class="example-3d">
        <swiper class="swiper" :options="swiperOption">
            <swiper-slide v-for="(slide, index) in text" :key="index">
                <h1 v-html="slide.header"></h1>
                <p class="desc" v-html="slide.desc"></p>
                <button
                    class="orange"
                    @click="showPopup('request')"
                >
                     
                </button>
            </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
export default {
    components: {
        Swiper,
        SwiperSlide
    },
    data() {
        return{
            currentSlide: 0,
            slideHeader: null,
            slideDesc: null,
            text: [
                {
                    header: '        ',
                    desc: '           ,   :  ,      .'
                },
                {
                    header: '  !',
                    desc: '       .    01.06.2020 .,  01.12.2020 .,      .'
                },
                {
                    header: '   ',
                    desc: '       <nobr><a href="tel:+79111286406">+7&#160;(911)&#160;128-64-06</a></nobr>'
                },
                {
                    header: '   !',
                    desc: '  -            .   ,  30-40%   !'
                }  
            ],
            swiperOption: {
                effect: 'coverflow',
                centeredSlides: true,
                slidesPerView: 'auto',
                loop: true,
                spaceBetween: 130,
                coverflowEffect: {
                    rotate: 0,
                    stretch: 22,
                    depth: 100,
                    modifier: 1,
                    slideShadows : true
                },
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true
                }
            }
        }
    },
    created() {
        this.slideHeader = this.text[this.currentSlide].header;
        this.slideDesc = this.text[this.currentSlide].desc;
    },
    methods: {
        isActive(index) {
            if (this.currentSlide == index) {
                return true;
            }
        },
        changeSlide(index) {
            this.currentSlide = index;
            this.slideHeader = this.text[index].header;
            this.slideDesc = this.text[index].desc;
        },
        showPopup(popup){
            this.$emit('showPopup', popup);
        }
    }
}
</script>
