    created() {
        this.revsCount = this.reviews.length;
        let summ = 0;
        for(let i = 0; i < this.revsCount; i++){
            summ += this.reviews[i].rating;
        }
        this.middleRating = parseFloat(summ / this.revsCount).toFixed(1);
        this.starsCount = Math.ceil(this.middleRating);
    },
    methods: {
        lessThanMax(text){
            if(text.length > this.maxLetters) return false;
            else return true;
        },
        cutText(text, index){
            let splitedText = text.split(" "), summ = 0, finalText = '';
            for(let i = 0; summ <= this.maxLetters; i++){
                summ += splitedText[i].length+1;
                finalText += splitedText[i]+' ';
            }
            finalText += '...';
            this.reviews[index].cuttedText = finalText;
        },
        showCutted(index){
            this.reviews[index].showedAll = true;
        },
        hideCutted(index){
            this.reviews[index].showedAll = false;
