<template>
    <transition name="op" appear>
        <div class="v-popup" :class="popup=='small' ? 'small' : 'right'" @click="closePopup">
            <transition v-if="popup == 'small'" name="op" appear>
                <div class="v-popup__block">
                    <slot name="header"></slot>
                    <slot name="content"></slot>
                </div>
            </transition>
            <transition v-else name="right" appear>
                <div class="v-popup__block" :class="popup == 'selectService0' || popup == 'selectService3' ? 'big' : ''">
                    <button class="close" @click="closePopup">
                        <img class="close" :src="require('@/assets/img/ui/Close.svg')"/>
                    </button>
                    <slot name="header"></slot>
                    <slot name="content"></slot>
                </div>
            </transition>
        </div>
    </transition>
</template>

<script>
export default {
    name: 'Popup',
    data(){
        return{

        }
    },
    props:[
        'animation',
        'popup'
    ],
    methods:{
        closePopup(e) {
            let t = e.target;
            if(t.classList.contains('v-popup') || t.classList.contains('close')){
                this.$emit('closePopup');
            }
        }
    }
}
</script>